import React from "react";
import { defineFeature, loadFeature } from "jest-cucumber";
import { fireEvent, render, waitFor, act } from "@testing-library/react";
import "text-encoding";
import "@testing-library/jest-dom/extend-expect";
import { Provider } from "react-redux";
import { configureStore } from "@reduxjs/toolkit";
import locationSlice, {
  addPreviousLocation,
  clearPreviousLocations,
  removePreviousLocation,
  setCurrentLocation,
} from "./../../reducers/reducer/locationSlice";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  MemoryRouter,
} from "react-router-dom";
import Locations from "../../Locations";
import Map from "../../Map";
import axios from "axios";
jest.mock("axios");
const feature = loadFeature("src/components/features/locations.feature");
const mockGeolocation = {
  getCurrentPosition: jest.fn(),
};
Object.defineProperty(global.navigator, "geolocation", {
  value: mockGeolocation,
  configurable: true,
});
defineFeature(feature, (test) => {
  test("Displaying Current Location", ({ given, when, then }) => {
    const store = configureStore({
      reducer: {
        locationList: locationSlice,
      },
    });
    const mockPosition = {
      coords: {
        latitude: 37.7749,
        longitude: -122.4194,
      },
    };
    (axios.get as jest.Mock).mockResolvedValueOnce({
      data: {
        results: {
          formatted_address: ["123 Street, Hyd"],
        },
      },
    });
    const screen = render(
      <Provider store={store}>
        <MemoryRouter>
          <Routes>
            <Route path="/" element={<Locations />} />
            <Route path="/map" element={<Map />} />
          </Routes>
        </MemoryRouter>
      </Provider>
    );
    given("the user is on the Locations page", () => {
      expect(screen).toBeTruthy();
      const currentAddress = screen.getByText("123 Street, Hyd");
    });
  });
});
